        transform: translate3d(0, -20%, 0);
        opacity: 0;
    }

    to {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}

@keyframes uppy-Dashboard-slideUpFadeOut {
    from {
        transform: translate3d(-50%, -50%, 0);
        opacity: 1;
    }

    to {
        transform: translate3d(-50%, -70%, 0);
        opacity: 0;
    }
}

@keyframes uppy-Dashboard-slideUpFadeOut--small {
    from {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }

    to {
        transform: translate3d(0, -20%, 0);
        opacity: 0;
    }
}

